'use client';

import { useState, useEffect } from 'react';
import Image from 'next/image';
import { TypingBox } from './components/ui/typing-box';
import { StatsChart } from './components/ui/stats-chart';
import MemoryMonitor from './components/ui/memory-monitor';
import { TypingAnalyzer } from './components/ui/typing-analyzer';
import NativeModuleStatus from './components/ui/native-module-status';
import { Settings } from './components/ui/settings';
import ActivityMonitor from './components/ui/activity-monitor';
import initStyles from './utils/init-styles';
import { 
  Home, 
  BarChart3, 
  Settings as SettingsIcon, 
  Activity, 
  Database,
  Cpu,
  Monitor,
  Menu,
  Bell,
  Zap
} from 'lucide-react';
import ClientIcon from './components/ui/client-icon';

interface Log {
  id?: string;
  content: string;
  keyCount: number;
  typingTime: number;
  timestamp: string;
  totalChars?: number;
}

type ActiveTab = 'home' | 'stats' | 'analysis' | 'settings';

export default function HomePage() {
  const [activeTab, setActiveTab] = useState<ActiveTab>('home');
  const [logs, setLogs] = useState<Log[]>([]);
  const [loading, setLoading] = useState(false);
  const [sidebarOpen, setSidebarOpen] = useState(true);
  const [imageError, setImageError] = useState(false);

  // 로그 데이터 로드 및 CSS 설정
  useEffect(() => {
    loadLogs();
    
    // CSS 스타일 강제 적용
    initStyles();
  }, []);

  const loadLogs = async () => {
    try {
      setLoading(true);
      const response = await fetch('/api/getLogs');
      const result = await response.json();
      
      if (result.success) {
        setLogs(result.data.logs || []);
      }
    } catch (error) {
      console.error('로그 로드 오류:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleTypingComplete = async (record: Omit<Log, 'id'>) => {
    try {
      const response = await fetch('/api/logs/save', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          ...record,
          totalChars: record.content.length
        }),
      });

      const result = await response.json();
      
      if (result.success) {
        // 로그 목록에 새 기록 추가
        setLogs(prev => [result.data, ...prev]);
      }
    } catch (error) {
      console.error('로그 저장 오류:', error);
    }
  };

  const navItems = [
    { id: 'home' as ActiveTab, label: '홈', icon: Home },
    { id: 'stats' as ActiveTab, label: '통계', icon: BarChart3 },
    { id: 'analysis' as ActiveTab, label: '분석', icon: Activity },
    { id: 'settings' as ActiveTab, label: '설정', icon: SettingsIcon },
  ];

  const renderContent = () => {
    switch (activeTab) {
      case 'home':
        return (
          <div className="space-y-6">
            {/* 메인 소개 섹션 */}
            <div className="bg-white dark:bg-gray-900 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700 p-6">
              <div className="flex items-center justify-between mb-4">
                <h1 className="text-2xl font-bold text-gray-900 dark:text-white">
                  Loop 6 - 타이핑 분석 시스템
                </h1>
                <div className="flex items-center gap-3">
                  {/* 실시간 알림 토글 */}
                  <button
                    className={`
                      relative inline-flex items-center
                      w-12 h-6 rounded-full transition-all duration-300 cursor-pointer
                      bg-gray-300 dark:bg-gray-600 hover:bg-blue-400 dark:hover:bg-blue-500
                      focus:outline-none focus:ring-0
                    `}
                    title="실시간 알림"
                    style={{ outline: 'none', boxShadow: 'none' }}
                  >
                    <Bell className="w-3 h-3 text-white ml-2" />
                    <div className="absolute right-1 w-4 h-4 bg-white rounded-full shadow-sm transition-transform duration-300"></div>
                  </button>
                  
                  {/* 애니메이션 효과 토글 */}
                  <button
                    className={`
                      relative inline-flex items-center
                      w-12 h-6 rounded-full transition-all duration-300 cursor-pointer
                      bg-gray-300 dark:bg-gray-600 hover:bg-purple-400 dark:hover:bg-purple-500
                      focus:outline-none focus:ring-0
                    `}
                    title="애니메이션 효과"
                    style={{ outline: 'none', boxShadow: 'none' }}
                  >
                    <Zap className="w-3 h-3 text-white ml-2" />
                    <div className="absolute right-1 w-4 h-4 bg-white rounded-full shadow-sm transition-transform duration-300"></div>
                  </button>
                </div>
              </div>
              <p className="text-gray-600 dark:text-gray-300 mb-6">
                실시간 타이핑 분석, GPU 가속, 메모리 최적화를 지원하는 고성능 데스크톱 애플리케이션입니다.
              </p>
              
              {/* 주요 기능 카드들 */}
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mt-6">
                <div className="bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-700 rounded-lg p-4">
                  <Activity className="w-6 h-6 text-blue-600 dark:text-blue-400 mb-2" />
                  <h3 className="font-semibold text-blue-900 dark:text-blue-200">타이핑 분석</h3>
                  <p className="text-sm text-blue-700 dark:text-blue-300 mt-1">실시간 타이핑 패턴 분석</p>
                </div>
                
                <div className="bg-green-50 dark:bg-green-900/20 border border-green-200 dark:border-green-700 rounded-lg p-4">
                  <Database className="w-6 h-6 text-green-600 dark:text-green-400 mb-2" />
                  <h3 className="font-semibold text-green-900 dark:text-green-200">Docs</h3>
                  <p className="text-sm text-green-700 dark:text-green-300 mt-1">문서 작업 최적화</p>
                </div>
                
                <div className="bg-orange-50 dark:bg-orange-900/20 border border-orange-200 dark:border-orange-700 rounded-lg p-4">
                  <Monitor className="w-6 h-6 text-orange-600 dark:text-orange-400 mb-2" />
                  <h3 className="font-semibold text-orange-900 dark:text-orange-200">Office</h3>
                  <p className="text-sm text-orange-700 dark:text-orange-300 mt-1">오피스 업무 지원</p>
                </div>
                
                <div className="bg-purple-50 dark:bg-purple-900/20 border border-purple-200 dark:border-purple-700 rounded-lg p-4">
                  <Cpu className="w-6 h-6 text-purple-600 dark:text-purple-400 mb-2" />
                  <h3 className="font-semibold text-purple-900 dark:text-purple-200">Coding</h3>
                  <p className="text-sm text-purple-700 dark:text-purple-300 mt-1">코딩 효율성 분석</p>
                </div>
              </div>
            </div>
            
            <TypingBox onComplete={handleTypingComplete} />
            
            {/* 실시간 통계 섹션 */}
            <div className="bg-white dark:bg-gray-900 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700 p-6">
              <h2 className="text-lg font-semibold text-gray-900 dark:text-white mb-4 flex items-center gap-2">
                <BarChart3 className="w-5 h-5 text-blue-500" />
                실시간 통계
              </h2>
              <StatsChart logs={logs} />
            </div>
            
            {/* 메모리 모니터 섹션 */}
            <MemoryMonitor />
            
            {logs.length > 0 && (
              <div className="bg-white dark:bg-gray-900 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700 p-6">
                <h2 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">최근 기록</h2>
                <div className="space-y-2">
                  {logs.slice(0, 5).map((log, index) => (
                    <div key={log.id || index} className="flex items-center justify-between p-3 bg-gray-50 dark:bg-gray-800 rounded-md">
                      <div className="flex-1">
                        <p className="text-sm text-gray-900 dark:text-gray-100 truncate">
                          {log.content.substring(0, 50)}...
                        </p>
                        <div className="flex items-center gap-4 mt-1 text-xs text-gray-500 dark:text-gray-400">
                          <span>타자: {log.keyCount}</span>
                          <span>시간: {log.typingTime}초</span>
                          <span>타수: {log.typingTime > 0 ? Math.round((log.keyCount / log.typingTime) * 60) : 0}타/분</span>
                        </div>
                      </div>
                      <span className="text-xs text-gray-400 dark:text-gray-500">
                        {new Date(log.timestamp).toLocaleString()}
                      </span>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        );

      case 'stats':
        return <StatsChart logs={logs} />;

      case 'analysis':
        return <TypingAnalyzer />;

      case 'settings':
        return <Settings />;

      default:
        return <div>페이지를 찾을 수 없습니다.</div>;
    }
  };

  return (
    <div className="min-h-screen bg-gray-100 dark:bg-gray-950">
      {/* Main Header with Navigation */}
      <header className="bg-white dark:bg-gray-900 border-b border-gray-200 dark:border-gray-700 shadow-sm sticky top-0 z-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            {/* Left: Logo and Brand */}
            <div className="flex items-center gap-3 flex-shrink-0">
              <div className="w-8 h-8 rounded-lg overflow-hidden bg-blue-500 flex items-center justify-center relative">
                {!imageError ? (
                  <Image 
                    src="/appIcon.webp" 
                    alt="Loop 6 Logo" 
                    width={32}
                    height={32}
                    className="w-full h-full object-cover"
                    onError={() => setImageError(true)}
                  />
                ) : (
                  <span className="text-white font-bold text-sm">L6</span>
                )}
              </div>
              <div className="hidden sm:block">
                <h1 className="text-lg font-bold text-gray-900 dark:text-white">Loop 6</h1>
                <span className="text-xs text-gray-500 dark:text-gray-400">타이핑 분석 시스템</span>
              </div>
            </div>
            
            {/* Center: Empty space for balance */}
            <div className="flex-1"></div>
            
            {/* Right: Navigation - 단순화된 스타일 */}
            <nav className="flex items-center space-x-6">
              {navItems.map((item) => (
                <button
                  key={item.id}
                  onClick={() => setActiveTab(item.id)}
                  className={`flex items-center gap-2 px-4 py-2 rounded-md text-sm font-medium transition-all duration-200 focus:outline-none ${
                    activeTab === item.id
                      ? 'bg-blue-500 text-white shadow-lg'
                      : 'text-gray-600 dark:text-gray-400 hover:bg-gray-100 dark:hover:bg-gray-800 hover:text-gray-900 dark:hover:text-gray-200'
                  }`}
                  style={{ boxShadow: 'none', outline: 'none' }}
                  onFocus={(e) => e.target.style.outline = 'none'}
                >
                  <ClientIcon icon={item.icon} className="w-4 h-4" />
                  <span>{item.label}</span>
                </button>
              ))}
            </nav>

            {/* Right: Status */}
            <div className="flex items-center gap-2 text-sm text-gray-500 dark:text-gray-400 flex-shrink-0">
              {loading && (
                <div className="flex items-center gap-2">
                  <div className="w-4 h-4 border-2 border-blue-500 border-t-transparent rounded-full animate-spin"></div>
                  <span className="hidden sm:inline">로딩 중</span>
                </div>
              )}
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        {loading ? (
          <div className="flex items-center justify-center h-64">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-500"></div>
            <span className="ml-2 text-gray-600 dark:text-gray-400">로딩 중...</span>
          </div>
        ) : (
          renderContent()
        )}
      </main>
    </div>
  );
}
